var packageType = [
    { value: 1, label: '' },
    { value: 2, label: 'lol' },
    { value: 3, label: '' },
    // { value: 4, label: '' },
    { value: 6, label: '' },
    { value: 7, label: 'egs' },
    { value: 10, label: '' },
];

function mergeArr() {
    return Array.prototype.concat.apply([], arguments);
}

export default {
    packageType: packageType,
    packageTypes: mergeArr({ value: 0, label: '' }, packageType),
    findPackageName(val) {
        var name = '';
        for (var i = 0; i < packageType.length; i++) {
            if(packageType[i].value === val) {
                name = packageType[i].label;
                break;
            }
        }
        return name;
    },
    forceType: [
        { value: 0, label: '' },
        { value: 1, label: '' },
        { value: 2, label: '' }
    ],
    defaultpath: ['[DESKTOP]', '[ROOT]', '[WINDOWS]', '[CUR]']
};



